<template>
  <div class="ui container">
    <div class="ui pointing secondary menu">
      <a class="active item" data-tab="departements">Departements</a>
      <a class="item" data-tab="filieres">Filieres</a>
    </div>

    <div class="ui active tab segment" data-tab="departements">
        <div class="ui category search">
          <div class="ui icon input">
            <i class="search icon"></i>
            <input type="text" v-model="searchQuery" placeholder="Rechercher par nom :" />
          </div>
        </div>

        <table class="ui fluid selectable table">
            <thead>
            <tr>
              <th>Nom du departement</th>
              <th>Responsable du departement</th>
              <th>Email du departement</th>
              <th>Actions</th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="row in resultQueryDepartement">
              <td>{{ row.nom }}</td>
              <td>{{ row.responsable }}</td>
              <td>{{ row.emailInstitutionnel }}</td>
              <td>
                <button class="compact circular ui medium icon positive  button" @click="toggleUpdateDepartement(row)"><i class="edit icon"></i></button>
                <button class="compact circular ui medium icon negative  button" @click="supprimerDepartement(row)"><i class="trash icon"></i></button>
              </td>
            </tr>
            </tbody>
            <tfoot class="full-width">
            <tr>
              <th colspan="10">
                <div class="ui  primary labeled icon button" @click="toggleInsertDepartement">
                  <i class="plus icon"></i> Ajouter un nouveau departement
                </div>
              </th>
            </tr>
            </tfoot>
          </table>

        <div id="updateModal" class="ui large modal">
          <div class="content">
            <div style="margin: 40px">
              <form class="ui form">
                <h4 class="ui dividing header">Departement Informations : </h4>
                <div class="fields">
                  <div class="sixteen wide field">
                    <label>Nom du departement : </label>
                    <div class="field">
                      <input type="text"  v-model="current_post.nom"   placeholder="Nom d'etudiant :">
                    </div>
                  </div>
                </div>
                <div class="fields">
                  <div class="sixteen wide field">
                    <label>Responsable du departement : </label>
                    <select v-model="current_post.responsable" class="ui fluid dropdown">
                      <option v-model="el.nom"  v-for="el in chefDepartements" >{{el.nom }}</option>
                    </select>
                  </div>
                </div>
                <div class="fields">
                  <div class="sixteen wide  field">
                    <label>Email institutionnel : </label>
                    <div class="field">
                      <input type="text"  v-model="current_post.emailInstitutionnel"   placeholder="Email institutionnel du departement">
                    </div>
                  </div>
                </div>
              </form>
            </div>
          </div>
          <div class="actions">
            <div class="ui positive button" @click="modifierDepartement(current_post)">Enregistrer</div>
          </div>
        </div>

        <div id="insertModal" class="ui large modal">
          <div class="content">
            <div style="margin: 40px">
              <form class="ui form">
                <h4 class="ui dividing header">Departement Information : </h4>
                <br>
                <div class="fields">
                  <div class="ten wide field">
                    <label>Nom du departement : </label>
                    <input type="text"  v-model="nom"   placeholder="Nom du departement :">
                  </div>
                  <div class="six wide field">
                    <label>Responsable du departement : </label>
                    <select v-model="responsable" class="ui fluid dropdown">
                      <option disabled selected value="">SVP selectionnez un enseignant</option>
                      <option v-for="el in chefDepartements" selected >{{ el.nom }}</option>
                    </select>
                  </div>
                </div>


                <div class="field">
                  <label>Email institutionnel du Responsable du departement : </label>
                  <div class="sixteen wide field">
                    <input type="text"  v-model="emailInstitutionnel"   placeholder="Email institutionnel du departement">
                  </div>
                </div>
              </form>
            </div>
          </div>
          <div class="actions">
            <div class="ui primary icon button" @click="ajouterDepartement">Enregistrer <i class="save icon"></i></div>
          </div>
        </div>
    </div>

    <div class="ui tab segment" data-tab="filieres">
      <div class="ui category search">
        <div class="ui icon input">
          <i class="search icon"></i>
          <input type="text" v-model="searchQueryFiliere" placeholder="Rechercher par nom :" />
        </div>
      </div>

      <table class="ui fluid selectable table">
        <thead>
        <tr>
          <th>Nom du filiere</th>
          <th>Responsable du filiere</th>
          <th>Email du filiere</th>
          <th>Actions</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="row in resultQueryFiliere">
          <td>{{ row.nom }}</td>
          <td>{{ row.responsable }}</td>
          <td>{{ row.emailInstitutionnel }}</td>
          <td>
            <button class="compact circular ui medium icon positive button" @click="toggleUpdateFiliere(row)"><i class="edit icon"></i></button>
            <button class="compact circular ui medium icon negative  button" @click="supprimerFiliere(row)"><i class="trash icon"></i></button>
          </td>
        </tr>
        </tbody>
        <tfoot class="full-width">
        <tr>
          <th colspan="10">
            <div class="ui  primary labeled icon button" @click="toggleInsertFiliere">
              <i class="plus icon"></i> Ajouter une nouvelle filiere
            </div>
          </th>
        </tr>
        </tfoot>
      </table>

      <div id="updateFiliereModal" class="ui large modal">
        <div class="content">
          <div style="margin: 40px">
            <form class="ui form">
              <h4 class="ui dividing header">Filiere Informations : </h4>
              <div class="fields">
                <div class="sixteen wide field">
                  <label>Nom du departement : </label>
                  <div class="field">
                    <input type="text"  v-model="current_filiere.nom" >
                  </div>
                </div>
              </div>
              <div class="fields">
                <div class="sixteen wide field">
                  <label>Responsable du departement : </label>
                  <select v-model="current_filiere.responsable" class="ui fluid dropdown">
                    <option v-model="el.nom"  v-for="el in chefFilieres" >{{el.nom }}</option>
                  </select>
                </div>
              </div>
              <div class="fields">
                <div class="sixteen wide  field">
                  <label>Email institutionnel : </label>
                  <div class="field">
                    <input type="text"  v-model="current_filiere.emailInstitutionnel"   placeholder="Email institutionnel du filiere">
                  </div>
                </div>
              </div>
            </form>
          </div>
        </div>
        <div class="actions">
          <div class="ui positive button" @click="modifierFiliere(current_filiere)">Enregistrer</div>
        </div>
      </div>

      <div id="insertFiliereModal" class="ui large modal">
        <div class="content">
          <div style="margin: 40px">
            <form class="ui form">
              <h4 class="ui dividing header">Filiere Information : </h4>
              <br>
              <div class="fields">
                <div class="ten wide field">
                  <label>Nom du filiere : </label>
                  <input type="text"  v-model="nomFiliere"   placeholder="Nom du departement :">
                </div>
                <div class="six wide field">
                  <label>Responsable du filiere : </label>
                  <select v-model="responsableFiliere" class="ui fluid dropdown">
                    <option disabled selected value="">SVP selectionnez un enseignant</option>
                    <option v-for="el in chefFilieres" selected >{{ el.nom }}</option>
                  </select>
                </div>
              </div>
              <div class="field">
                <label>Email institutionnel du Responsable du filiere : </label>
                <div class="sixteen wide field">
                  <input type="text"  v-model="emailInstitutionnelFiliere"   placeholder="Email institutionnel du filiere">
                </div>
              </div>
            </form>
          </div>
        </div>
        <div class="actions">
          <div class="ui positive button" @click="ajouterFiliere">Enregistrer</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "departements",

  data() {
    return {
      current_post: {},
      current_filiere: {},
      searchQuery:'',
      searchQueryFiliere:'',

      departements:[],
      filieres:[],
      chefDepartements:[],
      chefFilieres:[],

      displayData:'',
      message: '',

      nom :'',
      responsable :'',
      emailInstitutionnel :'',

      nomFiliere :'',
      responsableFiliere :'',
      emailInstitutionnelFiliere :''
    }
  },
  computed: {
    resultQueryDepartement()
    {
      if (this.searchQuery) {
        return this.departements.filter((item) => {
          return this.searchQuery.toLowerCase().split(' ').every(v => item.nom.toLowerCase().includes(v))
        })
      } else {
        return this.departements;
      }
    },
    resultQueryFiliere()
    {
      if (this.searchQueryFiliere) {
        return this.filieres.filter((item) => {
          return this.searchQueryFiliere.toLowerCase().split(' ').every(v => item.nom.toLowerCase().includes(v))
        })
      } else {
        return this.filieres;
      }
    }
  },
  methods: {
    toggleUpdateDepartement(post) {
      this.current_post = JSON.parse(JSON.stringify(post));
      $('#updateModal').modal('show')
    },
    toggleInsertDepartement() {
      $('#insertModal').modal('show')
    },

    toggleUpdateFiliere(post) {
      this.current_filiere = JSON.parse(JSON.stringify(post));
      $('#updateFiliereModal').modal('show')
    },
    toggleInsertFiliere() {
      $('#insertFiliereModal').modal('show')
    },

    ajouterFiliere() {
      this.$SHARED.dispatchAsyncPost(this.$SHARED.services.AJOUTER_FILIERE,
          {
            nom: this.nomFiliere,
            responsable:this.responsableFiliere,
            emailInstitutionnel:this.emailInstitutionnelFiliere
          }).then( data => {
        this.message = data.message;
      }, () => {})
    },
    supprimerFiliere(post) {
      this.$SHARED.dispatchAsyncDelete(this.$SHARED.services.SUPPRIMER_FILIERE,
          {id : post._id}).then(data => {
        this.message = data.message
      }, () => {})
    },
    modifierFiliere(current_filiere){
      this.$SHARED.dispatchAsyncPut(this.$SHARED.services.MODIFIER_FILIERE,
          {
            _id: current_filiere._id,
            nom: current_filiere.nom,
            responsable:current_filiere.responsable,
            emailInstitutionnel:current_filiere.emailInstitutionnel
          }).then( data => {
        this.message = data.message;
      }, () => {})
    },
    listerFilieres(){
      this.$SHARED.dispatchAsyncGet(this.$SHARED.services.LISTER_FILIERES ).then( data => {
        this.filieres = data;
      }, () => {})
    },
    ListerChefFiliere(){
      this.$SHARED.dispatchAsyncGet(this.$SHARED.services.LISTER_CHEFS_FILIERE ).then( data => {
        this.chefFilieres = data;
      }, () => {})
    },

    ajouterDepartement() {
      this.$SHARED.dispatchAsyncPost(this.$SHARED.services.AJOUTER_DEPARTEMENT,
          {
            nom: this.nom,
            responsable:this.responsable,
            emailInstitutionnel:this.emailInstitutionnel
          }).then( data => {
        this.message = data.message;
      }, () => {})
    },
    supprimerDepartement(post) {
      this.$SHARED.dispatchAsyncDelete(this.$SHARED.services.SUPPRIMER_DEPARTEMENT,
          {id : post._id}).then(data => {
        this.message = data.message
      }, () => {})
    },
    modifierDepartement(current_post){
      this.$SHARED.dispatchAsyncPut(this.$SHARED.services.MODIFIER_DEPARTEMENT,
          {
            _id: current_post._id,
            nom: current_post.nom,
            responsable:current_post.responsable,
            emailInstitutionnel:current_post.emailInstitutionnel
          }).then( data => {
        this.message = data.message;
      }, () => {})
    },
    ListerDepartements(){
      this.$SHARED.dispatchAsyncGet(this.$SHARED.services.LISTER_DEPARTEMENTS ).then( data => {
        this.departements = data;
      }, () => {})
    },
    ListerChefDepartement(){
      this.$SHARED.dispatchAsyncGet(this.$SHARED.services.LISTER_CHEFS_DEPARTEMENT ).then( data => {
        this.chefDepartements = data;
      }, () => {})
    }
  },
  mounted() {
    this.listerFilieres();
    this.ListerDepartements();
    this.ListerChefDepartement();
    this.ListerChefFiliere();

    $('.container .menu .item')
        .tab()
    ;
  }
}
</script>

